import "@servicenow/now-icon";
import "@servicenow/now-avatar";
import "@servicenow/now-button";
import "@servicenow/now-dropdown";
import "@servicenow/now-heading";
import "@servicenow/now-record-html-editor";

import { SnTimeAgo } from "sn-component-timeago";
import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { isEmpty, has, get, capitalize } from "lodash";
import { t } from 'sn-translate';
import {
	HTML_EDITOR_ACTION_PERFORMED_PRIVATE,
	SHOW_REPLY_BUTTON_CLICKED,
	HIDE_REPLY_BUTTON_CLICKED,
	CLOSE_EDITOR,
	SUBMIT_COMMENT,
	REPLY_COMMENT
} from "./constants";

const TextEditor = ({ text = "", dispatch, appendToPayload, value, language }) => {
	return (
		<div className="html-editor">
			<now-record-html-editor
				autofocus="true"
				placeholder={t("Type your comment here")}
				value={text}
				language={language}
			/>
			<div className="comment-buttons right">
				<now-button
					label={t("Cancel")}
					tooltip-content={t("Cancel")}
					size="md"
					variant="secondary"
					on-click={e =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED_PRIVATE, {
							action: CLOSE_EDITOR,
							...appendToPayload
						})
					}
				></now-button>
				<now-button
					label={t("Post")}
					tooltip-content={t("Post")}
					size="md"
					disabled={isEmpty(value) ? true : false}
					variant="primary"
					on-click={e =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED_PRIVATE, {
							action: SUBMIT_COMMENT,
							text: value,
							...appendToPayload
						})
					}
				></now-button>
			</div>
		</div>
	);
}

const getUserDetails = (item) =>
	item.level > 0 ? (
		<Fragment>
			<span className="author-container">
				<span className="author">{item.user.name}</span>
				<now-icon className="separator" icon="arrow-right-fill" size="sm"></now-icon>
				<span className="author">{item.parent_obj.user.name}</span>
			</span>
		</Fragment>
	) : (
			<Fragment>
				<span className="author">{item.user.name}</span>
			</Fragment>
		);

const getClass = (item, indentationLimit, expandedItems) => {
	let classes = "child";

	if (item.level > indentationLimit) {
		classes += " child-after-threshold";
	} else if (item.level == indentationLimit) {
		classes += " child-at-threshold";
	} else {
		classes += " child-before-threshold";
	}

	if (item.level % 2 == 0) {
		classes += " even-child";
	} else {
		classes += " odd-child";
	}

	if (item.level >= 1 && !expandedItems.includes(item.root.id)) {
		classes += " hide";
	}

	return classes;
}

const getRepliesButton = (item, expandedItems, dispatch, updateProperties, manageExpanded) => {
	if (get(item, "level", "") == 0 && get(item, "children.length", 0) > 0) {
		let showReplies = expandedItems.includes(item.id);

		return (
			<span>
				<now-button-bare
					data-testid="collapse"
					icon-start={
						showReplies
							? "circle-chevron-up-fill"
							: "circle-chevron-down-fill"
					}
					expanded={
						showReplies ? true : false
					}
					label={
						showReplies ? t("Hide replies") : t("Show replies")
					}
					tooltip-content={
						showReplies ? t("Hide replies") : t("Show replies")
					}
					size="md"
					variant="primary"
					on-click={
						() => {
							dispatch(showReplies ? HIDE_REPLY_BUTTON_CLICKED : SHOW_REPLY_BUTTON_CLICKED, {
								itemId: item.id
							});

							if (!manageExpanded) {
								if (showReplies)
									expandedItems.splice(expandedItems.indexOf(item.id), 1);
								else
									expandedItems.push(item.id);
								updateProperties({
									"expandedItems": expandedItems
								})
							}
						}
					}
				/>
			</span>
		)
	}

	return "";
}

const getTimeAgoString = (timeStamp) => {
	const options = {
		timeAgo: true,
		dateBoth: false,
		systemTime: true,
		dateOnly: false
	};

	return (
		<Fragment>
			<span className="time-stamp">
				{new SnTimeAgo({}, timeStamp, options).getTickTime().time}
			</span>
		</Fragment>
	);
}

const Comment = ({ item, className = "", isEditing = false, currentEditorId = "", expandedItems = [], level = "", dispatch, updateProperties, manageExpanded = false, value = "", language, reactionLabel, reactionIcon, indentationLimit, actions }) => {
	let visibleActions = [...actions];

	if (item.actions && item.actions.length > 0) {
		item.actions.map(function (action) {
			if (action.isHidden) {
				let index = actions.findIndex(element => element.id == action.id);
				visibleActions.splice(index, 1);
			}
		});
	}

	if (!has(item, "children"))
		item.children = [];

	return (
		<Fragment>
			<div className={className} data-testid={item.id}>
				<div className={item.level >= indentationLimit ? "comment-container-threshold" : "comment-container"}>
					<div className="avatar">
						<now-avatar
							aria-hidden="true"
							size="md"
							image-src={item.user.avatar}
							user-name={item.user.name}
						/>
					</div>
					<div className="comment-content">
						<div className="comment-meta-container">
							<span className="comment-publisher">
								{getUserDetails(item)}
								<span className="vertical-divider"></span>
								{getTimeAgoString(item.created)}
							</span>

							<span className="comment-actions">
								{
									visibleActions.length > 0 ?
										visibleActions.length > 1 ? (
											<now-dropdown
												bare
												hide-caret
												icon="ellipsis-h-outline"
												items={visibleActions}
												select="none"
												size="md"
												tooltip-content={t("More actions")}
												variant="secondary"
												append-to-payload={
													{
														comment: item
													}
												}
											/>
										) : (
												<now-button-iconic
													append-to-payload={
														{
															item: visibleActions[0],
															comment: item
														}
													}
													size="md"
													variant="tertiary"
													icon={visibleActions[0].icon}
													tooltip-content={t(visibleActions[0].label)}
													bare="true"
													hide-padding="true"
													config-aria={{ "aria-label": t(isEmpty(visibleActions[0].ariaLabel) ? visibleActions[0].label : visibleActions[0].ariaLabel) }}
													className="comment-action-icon"
												/>
											) : ""
								}
							</span>
						</div>
						<div className="comment-text">
							<span innerHTML={item.text}></span>
						</div>
						<div className="feedback-buttons">
							<span className="feedback-buttons-left">
								{
									reactionLabel && reactionIcon ? (
										<span className="feedback-button">
											<now-button-bare
												data-testid="reaction"
												appendToPayload={{ meta: "react", item: item }}
												label={capitalize(t(reactionLabel))}
												size="md"
												variant={item.isReacted ? "primary" : ""}
												icon-start={reactionIcon}
											></now-button-bare>
										</span>
									) : ""
								}

								<span className="feedback-button">
									<now-button-bare
										data-testid="reply"
										appendToPayload={{ meta: "reply", item: item }}
										label={t("Reply")}
										variant=""
										size="md"
										icon-start="chat-outline"
									></now-button-bare>
								</span>
							</span>
							<span className="feedback-buttons-right">
								{
									item.reactionCount ? (
										<span className="reaction-count">
											<span>
												{item.reactionCount}
											</span>
											<now-icon
												icon={reactionIcon}
												size="md"
												className="reaction-icon"
											></now-icon>
										</span>
									) : ""
								}

								{
									item.reactionCount && get(item, "level", "") == 0 && get(item, "children.length", 0) > 0 ? (
										<span className="vertical-divider">|</span>
									) : ""
								}

								{
									getRepliesButton(
										item,
										[...expandedItems],
										dispatch,
										updateProperties,
										manageExpanded
									)
								}
							</span>
						</div>

						{!isEditing && currentEditorId == item.id ? (
							<div className="html-editor-container">
								<TextEditor
									text=""
									dispatch={dispatch}
									appendToPayload={{ type: REPLY_COMMENT, itemId: item.id }}
									value={value}
									language={language}
								/>
							</div>
						) : (
								""
							)}
					</div>
				</div>
				{item.children.map((x, index) => {
					x.parent_obj = item;
					x.root = item.root;
					x.level = level + 1;
					return (
						<Comment
							item={x}
							isEditing={isEditing}
							currentEditorId={currentEditorId}
							expandedItems={expandedItems}
							manageExpanded={manageExpanded}
							language={language}
							reactionLabel={reactionLabel}
							reactionIcon={reactionIcon}
							indentationLimit={indentationLimit}
							actions={actions}
							level={level + 1}
							dispatch={dispatch}
							updateProperties={updateProperties}
							value={value}
							className={getClass(
								x,
								indentationLimit,
								expandedItems
							)}
						/>
					);
				})}
			</div>
		</Fragment>
	);
};

const snCommentsComponent = ({ value, properties: { items, isEditing, currentEditorId, expandedItems, manageExpanded, language, reactionLabel, reactionIcon, indentationLimit, actions } }, { dispatch, updateProperties }) => {
	let tempItems = JSON.parse(JSON.stringify(items));

	return (
		<Fragment>
			<div className="comments-container">
				{tempItems.map(item => {
					item.level = 0;
					item.root = item;
					return (
						<Comment
							item={item}
							isEditing={isEditing}
							currentEditorId={currentEditorId}
							expandedItems={expandedItems}
							manageExpanded={manageExpanded}
							language={language}
							reactionLabel={reactionLabel}
							reactionIcon={reactionIcon}
							indentationLimit={indentationLimit}
							actions={actions}
							level={0}
							dispatch={dispatch}
							updateProperties={updateProperties}
							value={value}
							className="parent"
						/>
					);
				})}
			</div>
		</Fragment>
	);
};

export default snCommentsComponent;
