const userSchema = {
    type: 'object',
    properties: {
        name: { type: 'string' },
        id: { type: 'string' },
        avatar: { type: 'string' }
    }
}

export const actionsSchema = {
    type: "array",
    items: {
        type: "object",
        properties: {
            id: { type: "string" },
            label: { type: "string", translatable: true },
            icon: { type: "string" },
            ariaLabel: { type: "string", translatable: true }
        }
    }
}

export const commentsSchema = {
    $id: 'commentsSchema',
    type: 'array',
    items: {
        type: 'object',
        properties: {
            id: { type: 'string' },
            created: { type: 'string' },
            text: { type: 'string', translatable: true },
            user: userSchema,
            reactionCount: { type: 'number' },
            isReacted: { type: 'boolean' },
            parent: { type: 'string' },
            children: { type: 'array', items: { $ref: 'commentsSchema#/items' } }
        }
    }
}