import "@servicenow/now-avatar";
import "@servicenow/now-button";
import "@servicenow/now-badge";
import "@servicenow/now-heading";
import "@servicenow/now-text-link";
import "@servicenow/now-record-html-editor";
import './sn-comments-internal';

import { isEmpty } from "lodash";
import { t } from 'sn-translate';
import {
	HTML_EDITOR_ACTION_PERFORMED,
	TOGGLE_DISPLAY_ALL_COMMENTS,
	SHOW_MORE_COMMENTS,
	CLOSE_EDITOR,
	SUBMIT_COMMENT,
	NEW_COMMENT
} from "./constants";

export const TextEditor = ({ dispatch, appendToPayload, value, language }) => {
	return (
		<div className="html-editor">
			<now-record-html-editor
				autofocus="true"
				value={value}
				language={language}
			/>

			<div className="comment-buttons right">
				<now-button
					label={t("Cancel")}
					tooltip-content={t("Cancel")}
					size="md"
					variant="secondary"
					on-click={e =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED, {
							action: CLOSE_EDITOR,
							...appendToPayload
						})
					}
				></now-button>
				<now-button
					label={t("Post")}
					tooltip-content={t("Post")}
					size="md"
					variant="primary"
					disabled={isEmpty(value) ? true : false}
					on-click={() =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED, {
							action: SUBMIT_COMMENT,
							text: value,
							...appendToPayload
						})
					}
				></now-button>
			</div>
		</div>
	);
}

const comments = (state, { dispatch }) => {
	return (
		<div className="nested-comments-container">
			<div className="header-container">
				<div className="header-left">
					<now-heading
						has-no-margin
						label={t("Comments")}
						variant="header-tertiary"
						className="heading"
					></now-heading>
					<now-badge
						round
						size="md"
						status="low"
						value={state.items.length}
						variant="secondary"
						className="badge"
					></now-badge>
				</div>
				{
					state.hasChildren ?
						<now-button
							expanded={state.expanded}
							data-testid="toggleReplies"
							label={state.expanded ? t("Hide all replies") : t("Show all replies")}
							tooltip-content={state.expanded ? t("Hide all replies") : t("Show all replies")}
							size="md"
							variant="tertiary"
							on-click={() => dispatch(TOGGLE_DISPLAY_ALL_COMMENTS)}
							className="header-right"
						></now-button> : ""
				}
			</div>

			<div className="create-container">
				{
					!state.openPrimaryEditor ? (
						<div className="create-comment-link">
							<now-avatar
								aria-hidden="true"
								size="md"
								image-src={state.properties.userInformation.avatar}
								user-name={state.properties.userInformation.fullName}
								className="avatar"
							/>
							<now-text-link
								label={t("Post a comment")}
								variant="primary"
								underline="false"
								href="javascript:void(0)"
							></now-text-link>
						</div>
					) : ""
				}

				{
					state.openPrimaryEditor ? (
						<div className="html-editor-container" >
							<span>{t("Compose a comment")}</span>
							<TextEditor
								dispatch={dispatch}
								appendToPayload={{ type: NEW_COMMENT }}
								value={state.value}
								language={state.properties.userInformation.language}
							/>
						</div>
					) : ""
				}
			</div>

			<sn-comments-internal
				items={state.items.filter((item, index) => index <= state.commentsToDisplay - 1)}
				isEditing={state.isEditing}
				currentEditorId={state.currentEditorId}
				expandedItems={state.expandedItems}
				manage-expanded
				language={state.properties.userInformation.language}
				reactionLabel={state.properties.reactionLabel}
				reactionIcon={state.properties.reactionIcon}
				indentationLimit={state.properties.indentationLimit}
				actions={state.properties.actions}
			></sn-comments-internal>

			{
				state.items.length > state.commentsToDisplay ?
					(
						<div className="show-more">
							<now-button-bare
								config-aria={{ "aria-label": t("Show more comments") }}
								hide-padding
								label={t("Show more")}
								size="md"
								variant="primary"
								on-click={() => dispatch(SHOW_MORE_COMMENTS)}
							></now-button-bare>
						</div>
					) : ""
			}
		</div>

	);
};

export default comments;
