import { createCustomElement } from "@servicenow/ui-core";
import { actions, actionHandlers } from "./actions";
import { commentsSchema, actionsSchema } from "./schemas/sn-nested-comments-schema";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import view from "./view";
import styles from "./styles.scss";

/**
 * @devsnc/sn-nested-comments is a generic seismic component which allows user to display nested comments. This component provides the following ability.
 * 1. Displays the multi-level comments based on the provided input.
 * 2. Provides the ability to add new comments and reply to the existing comments.
 * 3. Provides the ability to add actions to each comment which triggers events.
 * 4. Provides the ability to add & remove reaction (like, helpful, etc.) to the comments.
 * 
 * ```jsx
 * <sn-nested-comments
 *    user-information = {
 *        "language": "en",
 *        "avatar": "c148e1d13741310042106710ce41f149.iix?t=small",
 *        "fullName": "System Administrator"
 *    }
 *    comments={
 *        [{
 *            "id": "174df62204e19010f87700b07e812f8f",
 *            "created": "2020-06-25 04:55:40",
 *            "text": "<p>Sample Comment</p>",
 *            "user": {
 *                "name": "Bernard Laboy",
 *                "id": "ee826bf03710200044e0bfc8bcbe5de6",
 *                "avatar": ""
 *            },
 *            "reactionCount": 0,
 *            "isReacted": false,
 *            "parent": "",
 *            "children": []
 *        }]
 *    }
 *    actions={
 *        [{
 *            "id": "delete",
 *            "label": "Delete",
 *            "icon": "trash-outline",
 *            "ariaLabel": "Delete Comment"
 *        },
 *        {
 *            "id": "flag",
 *            "label": "Flag",
 *            "icon": "flag-outline"
 *        }]
 *    }
 *    initial-display-count="5"
 *    reaction-label="helpful"
 *    reaction-icon="thumbs-up-outline"
 *    indentation-limit="5"
 * ></sn-nested-comments>
 * ```
 *
 * @seismicElement sn-nested-comments
 * @summary Visual seismic component designed for UIB which allows users to displays nested comments.
 * @uib.label Nested comments
 * @uib.icon tile-uib-sn-comments-sm
 * @uib.description Visual seismic component designed for UIB which allows users to displays nested comments.
 * @uib.category component
 * @uib.associatedTypes global.core
 * @uib.properties comments, userInformation, actions, reactionLabel, reactionIcon, initialDisplayCount, indentationLimit
 */
createCustomElement("sn-nested-comments", {
  renderer: {
    type: snabbdom,
    view
  },
  properties: {
    /**
     * An array of the comments to display
     * 
     * @type {Comment[]}
     * @uib.label Comments
     * @uib.description An array of the comments to display
     */
    comments: { default: [], schema: commentsSchema },

    /**
     * Currently logged in user
     * 
     * @type {object}
     * @uib.label User information
     * @uib.description Currently logged in user
     */
    userInformation: { default: {} },

    /**
     * An array of supported comment actions
     * 
     * @type {Action[]}
     * @uib.label Actions
     * @uib.description An array of supported comment actions
     */
    actions: { default: [], schema: actionsSchema },

    /**
     * Label of reaction to be used
     * 
     * @type {string}
     * @uib.label Reaction label
     * @uib.description Label of reaction to be used
     */
    reactionLabel: {
      default: "like",
      schema: {
        type: "string",
        translatable: true
      }
    },

    /**
     * Icon of reaction to be used
     * 
     * @type {string}
     * @uib.label Reaction icon
     * @uib.description Icon of reaction to be used
     */
    reactionIcon: {
      default: "thumbs-up-outline",
      schema: {
        type: "string"
      }
    },

    /**
     * Number of comments to display on first load
     * 
     * @type {number}
     * @uib.label Initial display count
     * @uib.description Number of comments to display on first load
		 */
    initialDisplayCount: {
      default: 3,
      schema: {
        type: "number",
        minimum: 1
      }
    },

    /**
     * Nested comments indentation limit (Max: 12)
     * 
     * @type {number}
     * @uib.label Indentation limit
     * @uib.description Nested comments indentation limit (Max: 12)
     */
    indentationLimit: {
      default: 3,
      schema: {
        type: "number",
        minimum: 1,
        maximum: 7
      }
    }
  },
  dispatches: {
		/**
     * Event dispatched when a comment is submitted
     * 
     * @type {{content: string, parentId: string}}
     * @uib.description Event dispatched when a comment is submitted
		 */
    "SN_NESTED_COMMENTS#COMMENT_SUBMITTED": {
      schema: {
        type: "object",
        properties: {
          content: { type: "string" },
          parentId: { type: "string" }
        }
      }
    },
		/**
     * Event dispatched when an action is submitted
     * 
     * @type {{actionId: string, commentId: string}}
     * @uib.description Event dispatched when an action is submitted
		 */
    "SN_NESTED_COMMENTS#ACTION_SUBMITTED": {
      schema: {
        type: "object",
        properties: {
          actionId: { type: "string" },
          commentId: { type: "string" }
        }
      }
    },
		/**
     * Event dispatched when a reaction is toggled
     * 
     * @type {{reaction: boolean, commentId: string}}
     * @uib.description Event dispatched when a reaction is toggled
		 */
    "SN_NESTED_COMMENTS#REACTION_TOGGLED": {
      schema: {
        type: "object",
        properties: {
          reaction: { type: "boolean" },
          commentId: { type: "string" }
        }
      }
    }
  },
  initialState: {
    items: [],
    currentEditorId: "",
    expandedItems: [],
    isEditing: "",
    expanded: false,
    hasChildren: false,
    commentsToDisplay: 0,
    value: "",
    openPrimaryEditor: false
  },
  styles: styles,
  actions: actions,
  actionHandlers: actionHandlers
});