import { createCustomElement } from "@servicenow/ui-core";
import { SnTimeAgo } from 'sn-component-timeago';
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import view from "./view";
import { actionHandlers } from './actions'
import styles from "./styles.scss";

const timeAgoBehaviors = SnTimeAgo.getBehaviors();

createCustomElement("sn-comments-internal", {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		value: ""
	},
	properties: {
		items: { default: [] },
		isEditing: { default: "" },
		currentEditorId: { default: "" },
		expandedItems: { default: [] },
		manageExpanded: { default: false },
		language: { default: "en" },
		reactionLabel: { default: "" },
		reactionIcon: { default: "" },
		indentationLimit: { default: 1 },
		actions: { default: [] }
	},
	actionHandlers,
	styles: styles,
	behaviors: [...timeAgoBehaviors]
});


