import { get, isEmpty, isNaN } from "lodash";
import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

import {
	NOW_BUTTON_BARE_CLICKED,
	HTML_EDITOR_ACTION_PERFORMED,
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_TEXT_LINK_CLICKED,
	SHOW_REPLY_BUTTON_CLICKED,
	HIDE_REPLY_BUTTON_CLICKED,
	TOGGLE_DISPLAY_ALL_COMMENTS,
	SHOW_MORE_COMMENTS,
	NOW_RECORD_HTML_EDITOR_STAGED_VALUE_CHANGED,
	NOW_RECORD_HTML_EDITOR_VALUE_CHANGED,
	NOW_DROPDOWN_ITEM_CLICKED,
	SN_NESTED_COMMENTS_COMMENT_SUBMITTED,
	SN_NESTED_COMMENTS_ACTION_SUBMITTED,
	SN_NESTED_COMMENTS_REACTION_TOGGLED,
	CLOSE_EDITOR,
	SUBMIT_COMMENT,
	NEW_COMMENT,
	REPLY_COMMENT
} from "./constants";

const hasChildren = items => {
	for (let i = 0; i < items.length; i++) {
		if (get(items[i], "children.length", 0) > 0) {
			return true;
		}
	}
	return false;
};

const extractContent = (str) => {
	return (new DOMParser).parseFromString(str, "text/html").documentElement.textContent.toString().trim();
}

export const actions = {

};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect({ properties, updateProperties, updateState }) {
			let initialDisplayCount = (isNaN(properties.initialDisplayCount) || properties.initialDisplayCount < 0) ? 0 : parseInt(properties.initialDisplayCount);
			let commentsCount = properties.comments.length;
			let actions = !isEmpty(properties.actions) ? properties.actions : [];

			updateProperties({
				initialDisplayCount: initialDisplayCount,
				actions: actions
			});

			updateState({
				items: properties.comments,
				hasChildren: hasChildren(properties.comments),
				commentsToDisplay: commentsCount > initialDisplayCount ? initialDisplayCount : commentsCount
			});
		}
	},

	[COMPONENT_PROPERTY_CHANGED]: {
		effect({ state, properties, updateState }) {
			updateState({
				items: properties.comments,
				hasChildren: hasChildren(properties.comments)
			});

			let initialDisplayCount = properties.initialDisplayCount;
			let commentsToDisplay = state.commentsToDisplay;
			let commentsCount = properties.comments.length;

			if (commentsToDisplay < initialDisplayCount) {
				updateState({
					commentsToDisplay: commentsCount > initialDisplayCount ? initialDisplayCount : commentsCount
				});
			}
		}
	},

	[HTML_EDITOR_ACTION_PERFORMED]: {
		effect: ({ action, state, dispatch, updateState }) => {
			if (action.payload.action == CLOSE_EDITOR) {
				if (action.payload.type == NEW_COMMENT) {
					updateState({
						openPrimaryEditor: false
					});
				} else if (action.payload.type == REPLY_COMMENT) {
					updateState({
						path: "currentEditorId",
						value: "",
						operation: "set"
					});
				}
			} else if (action.payload.action == SUBMIT_COMMENT) {
				if (!isEmpty(action.payload.text)) {
					if (action.payload.type == REPLY_COMMENT) {
						dispatch(SN_NESTED_COMMENTS_COMMENT_SUBMITTED, {
							content: action.payload.text,
							parentId: action.payload.itemId
						});

						updateState({
							path: "currentEditorId",
							value: "",
							operation: "set"
						});

						if (state.items.find(x => x.id == action.payload.itemId)) {
							dispatch(SHOW_REPLY_BUTTON_CLICKED, { itemId: action.payload.itemId })
						}
					} else if (action.payload.type == NEW_COMMENT) {
						dispatch(SN_NESTED_COMMENTS_COMMENT_SUBMITTED, {
							content: action.payload.text,
							parentId: ""
						});

						updateState({
							openPrimaryEditor: false
						});

						updateState({
							path: "value",
							value: "",
							operation: "set"
						});
					}
				}
			}
		},
		stopPropagation: true
	},

	[SHOW_REPLY_BUTTON_CLICKED]: {
		effect: ({ action, state, updateState }) => {
			let tempState = {}
			if (state.expandedItems.indexOf(action.payload.itemId) == -1) {
				tempState.expandedItems = [...state.expandedItems, action.payload.itemId];
				updateState(tempState);
			}
		},
		stopPropagation: true
	},

	[HIDE_REPLY_BUTTON_CLICKED]: {
		effect: ({ action, state, updateState }) => {
			let tempArr = [...state.expandedItems];
			let tempState = {}
			let ind = tempArr.indexOf(action.payload.itemId);
			if (ind != -1) {
				tempArr.splice(ind, 1);
			}
			tempState.expandedItems = tempArr;
			updateState(tempState);
		},
		stopPropagation: true
	},

	[NOW_BUTTON_BARE_CLICKED]: {
		effect: ({ action, updateState, dispatch, state }) => {
			let commentId = get(action, "payload.item.id", "")
			if (get(action, "payload.meta") == "react") {
				if (get(action, "payload.item.isReacted", "") == true) {
					dispatch(SN_NESTED_COMMENTS_REACTION_TOGGLED, {
						reaction: false,
						commentId: commentId
					});
				} else if (get(action, "payload.item.isReacted", "") == false) {
					dispatch(SN_NESTED_COMMENTS_REACTION_TOGGLED, {
						reaction: true,
						commentId: commentId
					});
				}
			} else if (get(action, "payload.meta", "") == "reply") {
				if (
					isEmpty(state.currentEditorId) ||
					commentId != state.currentEditorId
				) {
					updateState({
						currentEditorId: commentId,
						isEditing: false
					});
				} else {
					updateState({
						currentEditorId: "",
						isEditing: false
					});
				}
			}
		},
		stopPropagation: true
	},

	[TOGGLE_DISPLAY_ALL_COMMENTS]: {
		effect({ updateState, state }) {
			if (state.expanded) {
				updateState({
					expanded: false,
					expandedItems: []
				});
			} else {
				let arr = [];
				state.items.forEach(x => {
					if (x.children.length > 0)
						arr.push(x.id);
				});
				updateState({
					expanded: true,
					expandedItems: arr
				});
			}
		},
		stopPropagation: true
	},

	[SHOW_MORE_COMMENTS]: {
		effect({ updateState, state: { commentsToDisplay, items }, properties: { initialDisplayCount } }) {
			if (commentsToDisplay + parseInt(initialDisplayCount) >= items.length) {
				updateState({
					commentsToDisplay: items.length
				})
			}
			else {
				updateState({
					commentsToDisplay: commentsToDisplay + parseInt(initialDisplayCount)
				});
			}
		},
		stopPropagation: true
	},

	[NOW_RECORD_HTML_EDITOR_STAGED_VALUE_CHANGED]: {
		effect({ action, updateState, state }) {
			let payloadValue = extractContent(action.payload.value);
			if (state.value != action.payload.value && (isEmpty(state.value) || isEmpty(payloadValue)))
				updateState({
					value: payloadValue
				});
		},
		stopPropagation: true
	},

	[NOW_RECORD_HTML_EDITOR_VALUE_CHANGED]: {
		effect({ action, updateState, state }) {
			if (!isEmpty(extractContent(action.payload.value)))
				updateState({
					value: action.payload.value
				});
		}
	},

	[NOW_BUTTON_ICONIC_CLICKED]: {
		effect({ action, dispatch }) {
			dispatch(SN_NESTED_COMMENTS_ACTION_SUBMITTED, {
				actionId: action.payload.item.id,
				commentId: action.payload.comment.id
			});
		},
		stopPropagation: true
	},

	[NOW_DROPDOWN_ITEM_CLICKED]: {
		effect({ action, dispatch }) {
			dispatch(SN_NESTED_COMMENTS_ACTION_SUBMITTED, {
				actionId: action.payload.item.id,
				commentId: action.payload.comment.id
			});
		},
		stopPropagation: true
	},

	[NOW_TEXT_LINK_CLICKED]: {
		effect({ updateState }) {
			updateState({
				openPrimaryEditor: true
			})
		},
		stopPropagation: true
	}
};
